* {
  box-sizing: border-box;
 /*  background: black; */
  -webkit-user-select: none;
  -moz-user-select: -moz-none;
  -ms-user-select: none;
  user-select: none;
}


.App {
  text-align: center;
  max-width: 370px;
  min-width: 300px;
  width: 80%;
  border: 2px dashed red;
  margin: auto;
  position: relative;
  top: 50px;
  border: 4px double rgb(0, 255, 0);
  border-radius: 15px;
  padding: 0;
  box-sizing: border-box;
}

h1 {
  margin-left: 20px;
  font-family: "Comic Sans MS", cursive, sans-serif;
  font-style: italic;
  color: rgb(0, 255, 0);
  text-align: left;
}

.done {
  background: rgb(136, 136, 136);
  color: rgb(248, 242, 242);
  text-decoration: line-through;
}

.inputAndButton {
  padding: 10px;
  display: flex;
}

button {
  cursor: pointer;
}

li span {

  color: rgba(255, 0, 0, 0.596);
  cursor: pointer;
  display: block;
  float: right;
  background: none;
}

li span:hover {
  color: rgb(255, 0, 0);
}


ul {
  padding: 5px 0;
  margin: 0;
  box-sizing: border-box;
}

li {
  list-style-type: none;
  margin: 0;
  cursor: pointer;
  word-wrap: break-word;
  overflow: auto;
  font-size: 20px;
  padding: 10px;
  height: auto;
  border: 1px solid white;
  background: yellow;
  border-radius: 15px;
  margin: 15px;
  text-align: left;
}

li:hover {
  border: 1px solid red;
}


